/* fist intro css */
.intro{
  position: relative;
 
}
.nameIntro{
  position: absolute;
  top: 200px;
  left: 200px;
  color:#FFFFFF;
  /* border: 1px solid red; */
  text-align: left;
  height: fit-content;
  height: 40vh;
  background-attachment: fixed;
  
}
.p-1{
  font-family: "Ubuntu",sans-serif;
  font-size: 40px;
 
}
.h-1{
  font-family: "Ubuntu",sans-serif;
  font-size: 70px;
  
}
.resume{
  width: 30%;
  height: 50px;
  border-radius: 10px;
  background-color: #DC143C;
  color: #FFFFFF;
  font-size: 30px;
  font-family: "Ubuntu",sans-serif;
  margin-left: 100px;
  text-align: center;
}



